<template>
  <div>
    <vHeader />
    <CountList :itemId="itemId" />
  </div>
</template>

<script>
import vHeader from 'base/vHeader/vHeader'
import CountList from 'base/CountList/CountList'

export default {
  name: 'CountPage',
  props: {
    id: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      itemId: +this.id
    }
  },
  components: {
    CountList,
    vHeader
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";

</style>


