<template>
  <div>
    <vHeader />
    <EditNewTable :itemId="itemId" />
  </div>
</template>

<script>
import vHeader from 'base/vHeader/vHeader'
import EditNewTable from 'base/EditNewTable/EditNewTable'

export default {
  name: 'NewListPage',
  props: {
    id: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      itemId: +this.id
    }
  },
  components: {
    EditNewTable,
    vHeader
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";

</style>


