<template>
  <div>
    <div class="view-header">
      <router-link to="/list">
        <el-button type="primary" icon="el-icon-arrow-left" class="main-page">回到首页</el-button>
      </router-link>
      <h3>{{tableContent.title}}</h3>
      <el-button type="primary" class="main-page" @click="copyHref">复制链接</el-button>
    </div>
    <el-form :model="form" ref="form" :rules="rules" v-model="form">
      <el-row type="flex" justify="center">
        <el-col :xs="22" :sm="18" :md="14" :lg="10">
          <el-card class="card-item" v-for="(item, index) in tableContent.questions" :key="item.id">

            <template v-if="item.type === 'radio'">
              <div slot="header">
                <span>Q{{index + 1}} {{item.title}}</span>
              </div>
              <el-form-item :prop="'q'+(index+1)">
                <el-radio-group v-model="form['q'+(index+1)]">
                  <el-radio :disabled="formDisable" class="input-item radio-item" :label="idx+1" v-for="(q, idx) in item.questions" :key="idx">{{q}}</el-radio>
                </el-radio-group>
              </el-form-item>
            </template>
            <template v-if="item.type === 'checkbox'">
              <div slot="header">
                <span>Q{{index + 1}} {{item.title}}</span>
                <i class="checkbox-mark">[多选题]</i>
              </div>
              <el-form-item :prop="'q'+(index+1)">
                <el-checkbox-group v-model="form['q'+(index+1)]">
                  <el-checkbox :disabled="formDisable" class="input-item" :label="idx+1" :name="'q'+(index+1)" v-for="(q, idx) in item.questions" :key="idx">{{q}}</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
            </template>
            <template v-if="item.type === 'textarea'">
              <div slot="header">
                <span>Q{{index + 1}} {{item.title}}</span><i v-show="item.mustFill" class="fill-mark">*</i>
              </div>
              <el-form-item :prop="'q'+(index+1)">
                <el-input
                  type="textarea"
                  :rows="3"
                  placeholder="请输入内容"
                  v-model="form['q'+(index+1)]"
                  :disabled="formDisable"
                  >
                </el-input>
              </el-form-item>
            </template>
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :xs="22" :sm="18" :md="14" :lg="10">
          <el-form-item>
            <el-button  :disabled="formDisable" type="primary" class="sub-btn" @click="submitHandler">提交表单</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- <custom-dialog :dialogVisible.sync="questionFullVisible" text="请将调查表填写完整" @dialogConfirm="questionNumVisible = false" @dialogCancel="questionNumVisible = false" /> -->
  </div>
</template>

<script>
import { getViewItem } from 'api/researchList.js'
import { setAnswers } from 'api/userAnswer.js'
import CustomDialog from 'base/CustomDialog/CustomDialog'

export default {
  name: 'ViewPage',
  props: {
    itemId: {
      type: String,
      default: ''
    }
  },
  data () {
    let form = {}
    let rules = {}

    // 表单检测
    // let checkFill = (rule, value, callback) => {
    //   console.log(value)
    //   if (!value || !value.length) {
    //     return callback(new Error('请完成填写'))
    //   } else {
    //     callback()
    //   }
    // }

    for (let i = 1; i <= 10; i++) {
      form[`q${i}`] = null
      // rules[`q${i}`] = [{
      //   required: true, message: '请完成本题的填写'
      // }]
    }
    return {
      itemData: {},
      tableContent: {},
      form: form,
      rules: rules,
      // questionFullVisible: false,
      formDisable: false // 默认不禁用表单
      // fillList: {}
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      getViewItem(this.itemId)
      .then((res) => {
        // console.log(res)
        if (res.errno) {
        this.itemData = res.data
        this.tableContent = JSON.parse(this.itemData.table_content)
        if (this.itemData.status === 0 || +this.itemData.expire_time < Date.now()) {
          // console.log('disabled')
          this.formDisable = true
        }
        this.tableContent.questions.forEach((item, index) => {
          this.rules[`q${index + 1}`] = [{
            required: true, message: '请完成本题的填写'
          }]
          if (item.type === 'checkbox') {
            this.form[`q${index + 1}`] = []
          } else if (item.type === 'textarea' && !item.mustFill) {
            console.log(item)
            this.form[`q${index + 1}`] = ''
            // this.fillList[`q${index + 1}`] = true
            this.rules[`q${index + 1}`] = [{
              required: false
            }]
          } else {
            this.form[`q${index + 1}`] = ''
          }
        })
        } else {
          this.$message.error('此调查表不存在');
          this.$router.push('/')
        }         
      })
    },
    submitHandler () {
      // console.log(this.fillList)
      if (this.validateForm('form')) {
        console.log('true')
        for (let attr in this.form) {
          if (this.form[attr] === null) {
            delete this.form[attr]
          }
        }
        setAnswers({
          id: this.itemData.id,
          form: this.form
        })
        .then((res) => {
          if (res.errno === 0) {
            this.$router.push('/complete')
          } else {
            this.$message({
              message: '提交失败',
              type: 'error'
            })
          }
        })
        .catch((err) => {
          this.$message({
            message: '提交失败',
            type: 'error'
          })
        })
      }
    },
    validateForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
      })
      return true
    },
    copyHref () {
      this.$clipboard(location.href)
      this.$message({
        message: '地址已被复制到剪贴板',
        type: 'success'
      });
    }
  },
  components: {
    CustomDialog
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";
  .main-page {
    
  }
  .view-header {
    // width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $blue;
    color: $white;
    h3 {
      font-size: 18px;
      font-weight: 700;
      line-height: 1.4;
      color: $white;
    }
  }
  .card-item {
    margin-top: 16px;
    .checkbox-mark {
      font-style: normal;
      color: $blue;
    }
    .fill-mark {
      display: inline-block;
      margin-left: 3px;
      transform: scale(1.3);
      color: $danger;
    }
    .input-item {
      display: block;
      margin: {
        left: 0;
        // top: -5px;
        // bottom: -10px;
      }
      &.radio-item {
        margin-bottom: 10px;
      }
    }
  }
  .sub-btn {
    margin-top: 12px;
    width: 100%;
  }
</style>



