<template>
  <el-row>
    <el-col :span="18" :offset="3">
      <el-card class="box-card" v-if="defaultList">
        <div>
          获取数据中...
        </div>
      </el-card>
      <el-card class="create-table" v-if="!defaultList">
          <!-- <h3 class="table-title" contenteditable>{{this.tableTitle}}</h3> -->
          <div class="table-title">
          <input class="title-content" type="text" v-model="tableTitle">
          </div>
        <div class="table-body">
          <div v-if="!currentQuestions.length && !showSelection" 
          @click="showQuestionSelect" class="question-area"><i class="el-icon-plus"></i>添加问题
          </div>
          <ul class="question-items-container" v-if="currentQuestions.length">
            <li class="question-list-item" v-for="(item, index) in currentQuestions" :key="index">
              <h3 class="question-title">
                Q{{index + 1}}
                <input name="title" v-model="item.title">
                <el-button v-if="item.type !== 'textarea'" type="text" class="ex-option2" @click.stop="addQuestionItem(index)">添加选项</el-button>
                <el-checkbox v-else class="ex-option" label="此题是否必填" v-model="currentQuestions[index].mustFill"></el-checkbox>
              </h3> 
              <template v-if="item.type === 'radio' || item.type === 'checkbox'">
                <p class="question-item" v-for="(questionItem, textIndex) in item.questions" :key="textIndex">
                  <i v-if="item.type === 'radio'" class="el-icon-fa-circle-thin"></i>
                  <i v-else class="el-icon-fa-square-o"></i>
                  <input class="question-item-text" v-model="item.questions[textIndex]" @blur="checkToDel(questionItem, index, textIndex)" />
                  <i class="el-icon-close" @click.stop="deleteItemText(index, textIndex)"></i>
                </p>
              </template>
              <template v-else>
                <el-input type="textarea" :readonly="true" class="textarea-input"></el-input>
              </template>
              <div class="operation-area">
                <el-button type="text" :disabled="index === 0" @click.stop="changeItemPosition(index, 'top')">上移</el-button>
                <el-button type="text" :disabled="index === currentQuestions.length - 1" @click.stop="changeItemPosition(index, 'bottom')">下移</el-button>
                <el-button type="text" @click.stop="reuse(index)">复用</el-button>
                <el-button type="text" @click.stop="deleteItem(index)">删除</el-button>
              </div>
            </li>
          </ul>
          <div v-if="showSelection" class="select-container">
            <div class="btn-container">
              <el-button icon="fa-genderless" @click.stop="addQuestion('radio')">单选</el-button>
              <el-button icon="fa-check-square" @click.stop="addQuestion('checkbox')">多选</el-button>
              <el-button icon="fa-file-text-o" @click.stop="addQuestion('textarea')">文本题</el-button>
            </div>
            <div class="add-area">
              <i class="el-icon-plus"></i>添加问题             
            </div>
          </div>
        </div>
        <div class="table-footer">
        <el-date-picker
          v-model="dateValue"
          type="date"
          placeholder="选择日期"
          :picker-options="pickerOptions"
          >
        </el-date-picker>
        <div class="publish-container">
          <el-button @click.stop="saveForm">保存问卷</el-button>
          <el-button @click.stop="confirmPublishForm">发布问卷</el-button>
        </div>
        </div>
      </el-card>
      <custom-dialog :dialogVisible.sync="questionNumVisible" text="问题数已经达到上限" @dialogConfirm="questionNumVisible = false" @dialogCancel="questionNumVisible = false" />
      <custom-dialog :dialogVisible.sync="validateVisible" text="请添加有效日期" @dialogConfirm="validateVisible = false" @dialogCancel="validateVisible = false" />
      <custom-dialog :dialogVisible.sync="questionMinVisible" text="至少要有一个问题" @dialogConfirm="questionMinVisible = false" @dialogCancel="questionMinVisible = false" />
      <custom-dialog :dialogVisible.sync="publishedVisible" text="是否发布问卷?" :extraContent="`此问卷截止日期为${new Date(dateValue).toLocaleDateString()}`" @dialogConfirm="publishForm" @dialogCancel="publishedVisible = false" />
    </el-col>
  </el-row>
</template>

<script>
// import _ from 'lodash'
import　CustomDialog from 'base/CustomDialog/CustomDialog'
import { mapActions, mapGetters } from 'vuex'
import { setSaveList, getListItem } from 'api/researchList'

export default {
  name: 'EditNewTable',
  props: {
    // questionsList: {
    //   type: Array,
    //   default: () => []
    // },
    itemId: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      listItem: {},
      tableTitle: '点击此处输入标题',
      currentQuestions: [],
      showSelection: false,
      dateValue: '',
      // defaultTime: this.dateValue || '',
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; // 8.64e7是一天
        }
      },
      defaultList: false,
      // 以下都是控制对话框显示
      questionNumVisible: false,
      validateVisible: false,
      questionMinVisible: false,
      publishedVisible: false
    }
  },
  computed: {
    ...mapGetters([
      // 'currentList',
      'userInfo'
      // 'listItem'
    ])
  },
  mounted () {
    if (this.itemId) {
      this.showSelection = true
      this.defaultList = true
      getListItem(this.itemId).then((res) => {
        if (res) {
          this.defaultList = false
          let tableData = Object.assign({}, res.data)
          let tableContentData = JSON.parse(tableData.table_content)
          this.tableTitle = tableContentData.title
          this.currentQuestions = tableContentData.questions
          this.dateValue = +tableData.expire_time
        } else {
          console.error('没有取得数据')
        }
      })
    } else {
      console.log('no itemId')
    }
  },
  methods: {
    showQuestionSelect () {
      this.showSelection = true
    },
    addQuestion (type) {
      let data = {}
      let questionIndex = this.currentQuestions.length
      if (questionIndex === 10) {
        // 弹窗, 达到数量限制
        return this.questionNumVisible = true
      }
      if (type === 'radio' || type === 'checkbox') {
        data = {
          type: type,
          title: type === 'radio' ? '单选题' : '多选题',
          // name: `q${questionIndex + 1}`,
          questions: [
            // {
            //   text:`选项1`,
            //   value: 1
            // },
            // {
            //   text:`选项2`,
            //   value: 2
            // }
              '选项1',
              '选项2'
          ]
        }
      } else if (type === 'textarea') {
        data = {
          type: 'textarea',
          title: '文本题',
          // name: `q${questionIndex + 1}`,
          mustFill: 0  
        }
      }
      this.currentQuestions.push(data)
    },
    addQuestionItem (index) {
      this.currentQuestions[index].questions.push(
      //   {
      //   text: "新选项",
      //   value: this.currentQuestions[index].questions.length ? this.currentQuestions[index].questions.slice().pop().value + 1 : 1
      // }
      '新选项'
      )
    },
    deleteItemText (index, textIndex) { // 删除问题选项
      let questionsArr = this.currentQuestions[index].questions
      questionsArr.splice(textIndex, 1);
      // questionsArr.forEach((item, itemIndex) => {
      //   item.value = itemIndex + 1
      // })
    },
    changeItemPosition (index, direction) {
      let item = this.currentQuestions.splice(index, 1)[0]
      if (direction === 'top') { // 上移
        // item.name = `q${index}`
        this.currentQuestions.splice(index - 1, 0, item)
      } else if (direction === 'bottom') { // 下移
        // item.name = `q${index + 2}`
        this.currentQuestions.splice(index + 1, 0, item)     
      }
      // this.currentQuestions[index].name = `q${index + 1}`
    },
    reuse (index) {
      let item = Object.assign({}, this.currentQuestions[index])
      // item.name = `q${index + 2}`
      this.currentQuestions.splice(index + 1, 0, item)
    },
    deleteItem (index) { // 删除问题
      this.currentQuestions.splice(index, 1)
      // this.currentQuestions.forEach((item, itemIndex) => {
      //   item.name = `q${itemIndex + 1}`
      // })
    },
    checkToDel (text, index, textIndex) {
      if (text.trim() === '') {
        this.deleteItemText(index, textIndex)
      }
    },
    saveForm () {
      if (!this.dateValue) {
        return this.validateVisible = true
      }
      if (this.currentQuestions.length === 0) {
        return this.questionMinVisible = true
      }
      this._setSaveItem(false)
      setSaveList(this.saveItem)
      // this.setCurrentList(this.currentList.concat(this.saveItem))
      .then((res) => {
        if (!res.errno) {
          this.$message({
            type: 'success',
            message: res.info
          })
          this.$router.push('/')
        } else {
          this.$message({
            type: 'error',
            message: res.info
          })
        }
      })
      .catch(() => {
        this.$message({
          type: 'error',
          message: '操作失败'
        })
      })
    },
    confirmPublishForm () {
      if (this.currentQuestions.length === 0) {
        return this.questionMinVisible = true
      }
      if (!this.dateValue) {
        return this.validateVisible = true
      }
      this.publishedVisible = true
    },
    publishForm () {
      this.publishedVisible = false
      this._setSaveItem(true)
      setSaveList(this.saveItem)
      .then((res) => {
        if (!res.errno) {
          this.$message({
            type: 'success',
            message: '发布成功'
          })
          this.$router.push('/')
        } else {
          this.$message({
            type: 'error',
            message: '发布失败'
          })
        }
      })
      .catch(() => {
        this.$message({
          type: 'error',
          message: '发布失败'
        })
      })
      // this.setCurrentList(this.currentList.concat(this.saveItem))
      // .then(() => {
      //   this.$message({
      //     type: 'success',
      //     message: '发布成功'
      //   })
      // })
      // .catch(() => {
      //   this.$message({
      //     type: 'error',
      //     message: '发布失败'
      //   })
      // })
    },
    _setSaveItem (published = false) {
      this.saveItem = {
        itemId: this.itemId,
        status: published ? 1 : 0,
        userId: this.userInfo.id,
        createTime: Date.now(),
        updateTime: Date.now(),
        expire: new Date(this.dateValue).getTime(),
        tableContent: {
          title: this.tableTitle,
          // expire: new Date(this.dateValue).getTime(),
          questions: this.currentQuestions
        }
      }
    },
    // _setPublishedItem () {
    //   this._setSaveItem()
    //   this.saveItem.status = 1
    // },
    ...mapActions([
      // 'listItem'
    ])
  },

  components: {
    CustomDialog
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";

.create-table {
  background: $white;
}
.table-header {
  transition: all .4s;
  margin: -8px 0;
}
.table-title {
  .title-content {
    text-align: center;
    padding: 8px 0;
    width: 100%;
    font-weight: 700;
    transition: .4s;
    &:hover {
      // color: $white;
      background: $transBlue;
    }
  }
  padding-bottom: 10px;
  border-bottom: 1px solid $exLightSilver;
}
.table-body {
  padding: 20px 0;
  border-bottom: 1px solid $exLightSilver;
  .question-area {
    padding: 20px;
    border: 1px solid $gray;
    background: $lightGray;
    text-align: center;
    cursor: pointer;
  }
  .question-items-container {
    .question-list-item {
      display: flex;
      flex-flow: column nowrap;
      padding: 10px;
      transition: all .4s;
      .operation-area {
        align-self: flex-end;
        transition: all .4s;
        opacity: 0;
      }
      &:hover {
        background: $transBlue;
      }
      &:hover input {
        background: transparent;
      }
      &:hover .ex-option {
        opacity: 1;
      }
      &:hover .operation-area {
        opacity: 1;
      }
    }
    .question-title {
      display: flex;
      align-items: center;
      font-weight: 700;
      // margin-top: -10px;
      margin-bottom: 10px;
      input {
        flex: auto;
        font-weight: 700;
        margin-left: 12px;
        // transition: all .4s;
      }
      .ex-option {
        margin-left: 6px;
      }
      .ex-option2 {
        @extend .ex-option;
        opacity: 0;
        transition: all .4s;
      }
    }
    .question-item {
      margin: {
        left: 24px;
        bottom: 10px;
      }
      &:hover .el-icon-close {
        opacity: 1;
      }
      .question-item-text {
        transition: all .4s;
      }
      .el-icon-close {
        transition: all .4s;
        font-size: 12px;
        opacity: 0;
        cursor: pointer;
      }
    }
    .textarea-input>textarea {
      user-select: none;
    }
  }
  .el-icon-plus {
    margin-right: 20px;
  }
  .select-container {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    margin-top: 8px;
    border: 1px solid $gray;
    // height: 120px;
    background: $lightGray;
    .btn-container {
      width: 100%;
      // height: 120px;
      padding: {
        top: 20px;
        bottom: 20px;
      }
      background: $white;
      text-align: center;
    }
    .add-area {
      padding: 20px 0;
      .el-icon-plus {
        margin-right: 12px;
      }
    }
  }
}
.table-footer {
  display: flex;
  justify-content: space-between;
  padding: {
    top: 20px;
  }
}
</style>



