<template>
  <ve-bar :data="chartData" :settings="chartSettings" :legend-visible="false" :title="chartTitle" height="100%" :grid="grid" :colors="colors" :after-config="afterConfig" 
  :tooltip="tooltip" ref="chart" :data-empty="dataEmpty" :extend="chartExtend">
  </ve-bar>
</template>

<script>
import 'echarts/lib/component/title'
import 'v-charts/lib/style.css'
  export default {
    props: {
      rowData: {
        type: Array,
        default () {
          return []
        }
      }
    },
    data () {
      this.chartExtend = {
        series (v) {
          v.forEach(i => {
            i.itemStyle = {
              normal: {
                borderWidth: 3,
                borderColor: '#58b7ff'
              }
            }
          })
          return v
        }
      }
      return {
        dataEmpty: false,
        grid: {
          show: true,
          top: '20%',
          bottom: '5%',
          right: '8%',
          left: '0%',
          backgroundColor: 'rgba(25, 255, 55, .3)',
          borderColor: 'transparent',
          borderWidth: 0
        },
        colors: ['#58b7ff', '#13ce66'],
        tooltip: {
          // showContent: false
          formatter (params) {
            return params.seriesName + ': ' + params.data
          }
        },
        chartData: {
          columns: ['title', 'valid'],
          rows: []
        },
        totalNum: 0
      }
    },
    created () {
      this.chartSettings = {
        max: [this.totalNum],
        label: {
          normal: {
            show: true,
            position: 'right',
            formatter: ({value}) => {
              return Math.round((value / this.totalNum).toFixed(2) * 100) + '%'
            }
          }
        }
      },
      this.chartTitle = {
        text: '有效回答占比',
        textStyle: {
          color: '#1d8ce0'
        }
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.$refs.chart.resize()
      })
    },
    methods: {
      afterConfig (options) { // 限制y轴文字长度
        options.xAxis.forEach((item) => {
          item.minInterval = 1
          item.max = this.totalNum
          item.splitLine = false
        })
        return options
      }
    },
    watch: {
      rowData (val) {
        if (val.length) {
          this.$set(this.chartData, 'rows', val)
          this.totalNum = val[0].total
          if (val.length === 1 && val[0].total === 0) {
            this.dataEmpty = true
          }
        }
      }
    }
  }
</script>


