<template>
  <el-row>
    <el-col :span="18" :offset="3">
      <el-card class="createTable">
        <el-button type="primary" icon="plus" @click="jumpCreate">新建问卷</el-button>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: 'CreateTable',
  methods: {
    jumpCreate() {
      this.$router.push({ path: 'create' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";

.createTable {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  background: $white;
}
</style>



