<template>
  <div>
    <div class="icon-container">
    <i class="question-icon el-icon-fa-question-circle"></i>
    </div>
    <el-row>
      <el-col :span="12" :offset="6">
        <el-tabs v-model="activeTab" @tab-click="handleTabClick">
          <el-tab-pane label="登陆" name="login">
            <!-- 登陆 -->
            <el-form :model="loginForm" :rules="rules" ref="loginForm" label-width="60px">
              <el-form-item label="用户名" prop="username">
                <el-input v-model="loginForm.username" placeholder="请输入用户名"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="loginForm.password" placeholder="请输入密码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('loginForm')">登陆</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="注册" name="register">
            <!-- 注册 -->
            <el-form :model="registerForm" :rules="rules2" ref="registerForm" label-width="60px">
              <el-form-item label="用户名" prop="username">
                <el-input v-model="registerForm.username" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="registerForm.password" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="确认密码" prop="checkPassword">
                <el-input type="password" v-model="registerForm.checkPassword" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('registerForm')">注册</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// import _ from 'lodash'
import　CustomDialog from 'base/CustomDialog/CustomDialog'
import { userLogin, userRegister, checkRegister } from 'api/account'
import jwt from 'jsonwebtoken'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'UserComponent',
    data () {
      const namePattern = /^[a-zA-Z0-9_]{3,16}$/
      const checkUsername1 = (rule, value, callback) => {
        if (!value.trim()) {
          return callback(new Error('请输入用户名'))
        } else {
          callback()
        }
      }
      const checkPassword1 = (rule, value, callback) => {
        if (!value.trim()) {
          return callback(new Error('请输入密码'))
        } else {
          callback()
        }
      }
      const checkUsername = (rule, value, callback) => {
        if (!value.trim()) {
          return callback(new Error('用户名不能为空'))
        }
        // setTimeout(() => {
          if (!namePattern.test(value)) {
            callback(new Error('请输入正确格式的用户名(3-16位英文或数字)'))
          } 

          checkRegister({username: value.trim()})
          .then((res) => {
            if (!res.success) {
              return callback(new Error(res.info))
            } else {
              callback()
            }
          })
        // }, 1000)
      }
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else if (value.trim().length < 7) {
          callback(new Error('密码的长度须不小于7位'));
        } else {
          if (this.registerForm.checkPassword !== '') {
            this.$refs.registerForm.validateField('checkPassword');
          }
          callback();
        }
      }
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.registerForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      }
      return {
        activeTab: 'login',
        action: 'login',
        hasLogined: Boolean(localStorage.getItem('__USERINFO__')) || false,
        // username: '',
        dialogVisible: false,
        loginForm: {
          username: 'abc',
          password: '1111111'
        },
        registerForm: {
          username: '',
          password: '',
          checkPassword: ''
        },
        rules: {
          username: [
            {validator: checkUsername1, trigger: 'blur'}
          ],
          password: [
            {validator: checkPassword1, trigger: 'blur'}
          ]
        },
        rules2: {
          password: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPassword: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          username: [
            { validator: checkUsername, trigger: 'blur' }
          ]
        }
      }
    },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  methods: {
    handleTabClick (tab, event) {
      // console.log(tab.name)
      if (tab.name === 'register') {
        this.action = 'register'
      } else if (tab.name === 'login') {
        this.action = 'login'
      }
    },
    submitForm (formName) { // 提交注册和登陆的处理
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (formName === 'registerForm') { // 注册
            userRegister(this.registerForm)
            .then((res) => {
              if (res.success === true) {
                this.hasLogined = true
                this.dialogVisible = false
                this.setUserInfo(jwt.verify(res.token, res.secret))
                localStorage.setItem('__USERTOKEN__', res.token)
                this.$message.success(res.info)
                this.$router.push('/list')
              } else {
                this.hasLogined = false
                // this.dialogVisible = false
                this.$message.error(res.info)
              }
            })
            .catch((err) => {
              this.$message.error('注册失败')
              this.hasLogined = false
            })
          }
          else if (formName === 'loginForm') { // 登陆
            this.userLoginHandler(this.loginForm)
          }
        } else {
          console.log('error submit!!')
          return false;
        }
      });
    },
    // userLogout () {
    //   this.removeUserInfo()
    //   this.hasLogined = false
    //   localStorage.removeItem('__USERTOKEN__')
    //   this.$message.success('已登出')
    //   this.$router.push('/')
    // },
    userLoginHandler (data) {
      userLogin(data) 
      .then((res) => {
        if (res.success === true) {
          this.hasLogined = true
          this.dialogVisible = false
          this.setUserInfo(jwt.verify(res.token, res.secret))
          localStorage.setItem('__USERTOKEN__', res.token)
          this.$message.success(res.info)
          this.$router.push('/list')
        } else {
          this.hasLogined = false
          // this.dialogVisible = false
          this.$message.error(res.info)
        }
      })
      .catch((err) => { // 登陆失败
        this.$message.error('登陆失败')
        this.hasLogined = false
      })
    },
    ...mapActions([
      'setUserInfo',
      'removeUserInfo'
    ])
  },

  components: {
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";

.icon-container {
  // display: flex;
  // justify-content: center;
  // align-items: center;
  text-align: center;
  margin: {
    top: 30px;
    bottom: 20px;
  }
}
.question-icon::before {
  font-size: 120px;
  color: $blue;
}
</style>



