<template>
  <div class="current-list">
    <div v-if="defaultList">
      <el-row>
        <el-col :span="18" :offset="3">
          <el-card class="box-card">
            <div>
              获取数据中
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <!-- <template v-if="currentList"> -->
    <CreateTable v-if="!this.defaultList && currentList.length === 0" />
    <el-row  v-if="currentList.length">
      <el-col :span="18" :offset="3" class="table-container">
        <el-button class="new-research" type="primary" icon="plus" size="small" @click="jumpCreate">新建问卷</el-button>
      <el-table
        :data="tableData"
        :row-key="getRowKey"
        style="width: 100%">
        <!-- <el-table-column
          prop="title"
          width="60">
          <template scope="scope">
            <el-checkbox v-model="checkList[scope.$index]"></el-checkbox>
          </template>
        </el-table-column> -->
        <el-table-column
          prop="title"
          label="标题"
          >
        </el-table-column>
        <el-table-column
          prop="date"
          label="时间"
          >
        </el-table-column>
        <el-table-column
          label="状态"
          >
          <template scope="scope">
            <span :class="{ publishing: scope.row.status === statusList[1], published: scope.row.status === statusList[2]}">{{scope.row.status}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="operation"
          label="操作">
          <el-button type="primary" icon="plus">新建问卷</el-button>
          <template scope="scope">
            <el-button v-if="scope.row.status === statusList[0]" type="text" size="small" @click="editTable(scope.row.id, currentList[scope.$index])">编辑</el-button>
            <el-button v-if="scope.row.status === statusList[0]" type="text" size="small" @click="deleteHandler(scope.row.id)" class="delete-btn">删除</el-button>
            <router-link :to="'/view/'+scope.row.id">
              <el-button type="text" size="small">查看问卷</el-button>
            </router-link>
            <el-button v-if="scope.row.status !== statusList[0]" type="text" size="small" @click="countData(scope.row.id, currentList[scope.$index])">查看数据</el-button>
          </template>
        </el-table-column>
      </el-table>
      </el-col>
    </el-row>
    <!-- <el-row v-if="currentList.length">
      <el-col :span="18" :offset="3" class="col-container">
        <el-checkbox @change="switchChecked" ref="checkBtn">全选</el-checkbox>
        <el-button type="primary" size="small" :disabled="!checkAll">删除勾选的未发布问卷</el-button>
      </el-col>
    </el-row> -->
    <el-row v-if="currentList.length">
      <el-col :span="18" :offset="3" class="col-container pagination-container">
        <el-pagination @current-change="changeCurrentList"
          layout="prev, pager, next"
          :total="totalPage">
        </el-pagination>
    　</el-col>
    </el-row>
    <!-- </template> -->
    <custom-dialog :dialogVisible.sync="deleteItemVisible" text="确定要删除此条目吗?" @dialogConfirm="deleteListItem" @dialogCancel="deleteItemVisible = false" />
  </div>
</template>

<script>
import CreateTable from 'base/CreateTable/CreateTable'
import { mapActions, mapGetters, mapMutations } from 'vuex'
import { getDefaultList, deleteItem } from 'api/researchList.js'
import　CustomDialog from 'base/CustomDialog/CustomDialog'

export default {
  name: 'CurrentList',
  // props: {
  //   showStatus: {
  //     type: Array,
  //     default: [0, 1]
  //   },
  //   showExpired: {
  //     type: Boolean,
  //     default: true
  //   }
  // },
  data () {
    return {
      currentList: [],
      tableData: [],
      isIndeterminate: true,
      // checkAll: true
      checkList: [],
      checkAll: false,
      pageSize: 10,
      totalPage: 0,
      currentPage: 1,
      deleteItemVisible: false,
      defaultList: true, 
    }
  },
  mounted () {
    this.statusList = ['未发布', '发布中', '已结束']
    this.getCurrentList(this.currentPage)
  },
  computed: {
    ...mapGetters([
      // 'currentList'
      'userInfo'
    ])
  },
  methods: {
    jumpCreate () {
      this.$router.push({ path: 'create' })
    },
    switchChecked () {
      if (!this.checkAll) {
        this.checkList = new Array(this.currentList.length).fill(true)
        this.checkAll = true
      } else {
        this.checkList = new Array(this.currentList.length).fill(false)
        this.checkAll = false
      }
    },
    getCurrentList (currentPage) {
      // let userInfo = JSON.parse(localStorage.getItem('__USERINFO__'))
      // console.log(userInfo)
      getDefaultList(currentPage, this.pageSize, this.userInfo.id)
      .then((res) => {
        // console.log(res)
        if (res.errno === 0) {
          this.defaultList = false
          this.currentList = res.data
          this.totalPage = res.count
        } else {
          this.defaultList = false
          return
        }
      })
      .then(() => {
        this.tableData = this.currentList.map((item, index) => {
          return {
            title: JSON.parse(item.table_content).title,
            date: new Date(+item.create_time).toLocaleDateString(),
            status: new Date(+item.expire_time) < Date.now() ? '已结束' : this.statusList[item.status],
            id: item.id
          }
        })      
      })
      .then(() => {
        this.checkList = new Array(this.currentList.length).fill(false)
      })
    },
    changeCurrentList (currentPage) {
      this.currentPage = currentPage
      this.getCurrentList(currentPage)
    },
    editTable (id, data) { // 编辑
      // console.log(data)
      // this.setListItem(data)
      this.$router.push(`/edit/${id}`)
    },
    countData (id, data) { // 查看数据
      // this.setListItem(data)
      this.$router.push(`/count/${id}`)
    },
    deleteHandler (id) {
      // return
      this.deleteId = id
      this.deleteItemVisible = true
    },
    deleteListItem () {
      deleteItem(this.deleteId)
      .then((res) => {
        if (!res.errno) {
          this.$message({
            type: 'success',
            message: res.info
          })
        } else {
          this.$message({
            type: 'error',
            message: res.info
          })
        }
      })
      .catch(() => {
        this.$message({
          type: 'error',
          message: '操作失败'
        })
      })
      this.getCurrentList(this.currentPage)
      this.deleteItemVisible = false
    },
    getRowKey (row) {
      return row.id
    },
    ...mapMutations({
      setListItem: 'SET_LIST_ITEM'
    }),
    ...mapActions([
      // 'setCurrentList'
    ])
  },

  components: {
    CreateTable,
    CustomDialog
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";
.publishing {
  color: $success;
}
.published {
  color: $danger;
}
.table-container {
  position: relative;
}
.new-research {
  position: absolute;
  right: 20px;
  top: 6px;
  z-index: 10;
}
.col-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: {
    left: 19px;
    right: 6px;
  }
  height: 36px;
  &.pagination-container {
    padding: 0;
    justify-content: center;
    margin-top: 20px;
  }
}
.delete-btn {
  margin-left: 0;
}
</style>



