<template>
  <div>
    <template v-if="answerList.length">
      <div class="table-title">
        {{tableContentData.title}}
      </div>
      <el-row>
        <el-col :span="18" :offset="3">
          <el-card class="box-card" v-for="(item, index) in tableContentData.questions" :key="item.id">
            <div slot="header" class="clearfix">
              Q{{index + 1}} {{item.title}}<i v-if="item.type === 'checkbox'" class="checkbox-mark">[多选题]</i><i v-if="item.mustFill" class="fill-mark">*</i>
            </div>
            <div class="right-col" ref="chartContainer">
              <CustomChartsBar v-if="item.type === 'radio'" :rowData="rowData[index]" />
              <CustomChartsPie v-if="item.type === 'checkbox'" :rowData="rowData[index]" />
              <CustomChartsBarText v-if="item.type === 'textarea'" :rowData="rowData[index]" />
            </div>
          </el-card>
        </el-col>
      </el-row>
    </template>
    <div v-if="!answerList.length">
      <el-row>
        <el-col :span="18" :offset="3">
          <el-card class="box-card">
            <div>
              {{defaultText}}
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
// import _ from 'lodash'
// import　CustomDialog from 'base/CustomDialog/CustomDialog'
import { mapActions, mapGetters, mapMutations } from 'vuex'
import { getListItem, getAnswers } from 'api/researchList'
import CustomChartsPie from 'base/CustomCharts/CustomChartsPie'
import CustomChartsBar from 'base/CustomCharts/CustomChartsBar'
import CustomChartsBarText from 'base/CustomCharts/CustomChartsBarText'

export default {
  name: 'CountList',
  props: {
    itemId: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      // tableData: {},
      // tableTitle: '',
      defaultText: '正在获取统计数据...', 
      tableContentData: {},
      answerList: [],
      // dataList: [],
      titleText: {},
      rowData: []
    }
  },
  mounted () {
    getListItem(this.itemId)
    .then((res) => {
      if (res) {
        this.tableContentData = JSON.parse(res.data.table_content)
      } else {
        return console.error('没有取得问题列表')
      }
    })
    .then(() => {
      return getAnswers(this.itemId)
    })
    .then((res) => {
    // console.log(res)
    if (res.data.length) {
      // console.log(this.tableContentData)
      this.answerList = res.data.map((item, index) => {
        return JSON.parse(item.user_answer)
      })
    } else {
      this.defaultText = '本调查表暂时还没有统计数据'
      console.log('没有取得数据')
      }
    })
    .then(() => {
      this.handlerAnswerList()
    })
    .catch((err) => {
      console.error(err)
    })
  },
  methods: {
    handlerAnswerList () {
      this.tableContentData.questions.forEach((item, index) => {
        // console.log(item)
        if (item.type === 'radio') {
          // this.$set(this.heightStyle, index, `${80 + 30 * item.questions.length}px`)
          this.$refs.chartContainer[index].style.height = `${80 + 30 * item.questions.length}px`
          this.rowData.push(item.questions.map((questionItem) => {
            return {
              'title': questionItem,
              'total': 0
            }
          }))
        } else if (item.type === 'checkbox') {
          // this.$set(this.heightStyle, index, '300px')
          this.$refs.chartContainer[index].style.height = `300px`
          this.rowData.push(item.questions.map((questionItem) => {
            return {
              'title': questionItem,
              'total': 0
            }
          }))
        } else if (item.type === 'textarea') {
          // this.$set(this.heightStyle, index, '200px')
          this.$refs.chartContainer[index].style.height = `80px`
          this.titleText[index] = '有效回答占比'
          this.rowData.push([{
            'title': '',
            'valid': 0,
            'total': 0
          }])
        }
      })
      // console.log(this.answerList)
      this.answerList.forEach((item, index) => {
        for (let attr in item) {
          // console.log(attr)
          let questionIndex = Number(attr.slice(1) - 1)
          if (typeof item[attr] === 'number') { // 
            // console.log(item[attr])
            // console.log(this.rowData[questionIndex][item[attr] - 1])
            this.rowData[questionIndex][item[attr] - 1].total += 1
          } else if (Array.isArray(item[attr])) {
            item[attr].forEach((listItem) => {
              this.rowData[questionIndex][listItem - 1].total += 1
            })
          } else if (typeof item[attr] === 'string') {
            if (item[attr] !== '') {
              this.rowData[questionIndex][0].valid += 1
              this.rowData[questionIndex][0].total += 1
            } else {
              this.rowData[questionIndex][0].total += 1
            }
          }
        }
      })
    }
  },
  components: {
    CustomChartsBar,
    CustomChartsBarText,
    CustomChartsPie
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";
.table-title {
  text-align: center;
  padding: 8px 0;
  width: 100%;
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 16px;
  padding-bottom: 10px;
  color: $darkBlue;
}
.box-card {
  margin-bottom: 20px;
  // .left-col {
  //   display: inline-block;
  //   width: 30%;
  // }
  .right-col {
    // float: right;
    // width: 70%;
    height: 200px;
  }
}
.checkbox-mark {
  margin-left: 5px;
  font-style: normal;
  color: $blue;
}
.text-item {
  margin-top: 6px;
}
.fill-mark {
  display: inline-block;
  margin-left: 3px;
  transform: scale(1.3);
  color: $danger;
}
</style>



