import axios from 'axios'
import { baseUrl } from 'api/config'
import Qs from 'qs'

export function getDefaultList (currentPage, pageSize, userId) {
  let userToken = localStorage.getItem('__USERTOKEN__')
  return axios.get(`${baseUrl}/api/list/${userId}`, {
    params: {
      currentPage: currentPage,
      pageSize: pageSize
    },
    headers: {'Authorization': `Bearer ${userToken}`}
  })
  .then((res) => {
    // console.log(res)
    return Promise.resolve(res.data)
  })
}

export function setSaveList (data) {
  let userToken = localStorage.getItem('__USERTOKEN__')
  return axios.post(`${baseUrl}/api/savelist`, data, {
    headers: {'Authorization': `Bearer ${userToken}`}
  })
  .then((res) => {
    return Promise.resolve(res.data)
  })
}

export function deleteItem (id) {
  let userToken = localStorage.getItem('__USERTOKEN__')
  return axios.get(`${baseUrl}/api/delete/${id}`, {
    headers: {'Authorization': `Bearer ${userToken}`}
  })
  .then((res) => {
    return Promise.resolve(res.data)
  })
}

export function getViewItem (id) {
  return axios.get(`${baseUrl}/view/${id}`)
  .then((res) => {
    return Promise.resolve(res.data)
  })
}

export function getListItem (id) {
  let userToken = localStorage.getItem('__USERTOKEN__')
  return axios.get(`${baseUrl}/api/listitem/${id}`, {
    headers: {'Authorization': `Bearer ${userToken}`}
  })
  .then((res) => {
    return Promise.resolve(res.data)
  })
}

export function getAnswers (id) {
  let userToken = localStorage.getItem('__USERTOKEN__')
  return axios.get(`${baseUrl}/api/answerlist/${id}`, {
    headers: {'Authorization': `Bearer ${userToken}`}
  })
  .then((res) => {
    return Promise.resolve(res.data)
  })
}


