<template>
  <ve-pie :data="chartData" :settings="chartSettings" :legend-visible="false" :title="chartTitle" height="100%"
  :tooltip="tooltip" ref="chart"
  >
  </ve-pie>
</template>

<script>
import 'echarts/lib/component/title'

  export default {
    props: {
      rowData: {
        type: Array,
        default () {
          return []
        }
      }
    },
    data () {
      return {
        // dataEmpty: false
        chartTitle: {
          text: '数据占比',
          textStyle: {
            color: '#1d8ce0'
          }
        },
        // colors: ['#20a0ff', '#13ce66', '#f7ba2a', '#ff4949', '#61a0a8', '#d48265', '#91c7ae','#749f83'],
        tooltip: {
          // showContent: false
          formatter: '{c}'
        },
        chartData: {
          columns: ['title', 'total'],
          rows: []
        }
      }
    },
    created: function () {
      this.chartSettings = {
        // xAxisType: ['percent'],
        // max: [10],
        label: {
          normal: { 
            show: true, 
            // position: 'right',
            formatter: ({name}) => {
              if (name.length > 15) {
                return name.slice(0, 15) + '...'
              }
              return name
            }
          },
          // emphasis: {show: true},
          legendLimit: 8
        },
        offsetY: 170
      }
    },
    watch: {
      rowData (val) {
        if (val.length) {
          this.$set(this.chartData, 'rows', val)
        }
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.$refs.chart.resize()
      })
    },
    methods: {
    }
  }
</script>


