import axios from 'axios'
import { baseUrl } from 'api/config'
import Qs from 'qs'

export function userLogin (data) {
  return axios.post(`${baseUrl}/user`, Qs.stringify(data), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
  .then((res) => {
    return Promise.resolve(res.data)
  })
}

export function userRegister (data) {
  return axios.post(`${baseUrl}/reg`, Qs.stringify(data), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
  .then((res) => {
    return Promise.resolve(res.data)
  })
}

export function checkRegister (data) {
  return axios.post(`${baseUrl}/check`, Qs.stringify(data), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
  .then((res) => {
    return Promise.resolve(res.data)
  })
}


