<template>
  <div>
    <div class="view-header">
      <router-link to="/list">
        <el-button type="primary" icon="el-icon-arrow-left" class="main-page">回到首页</el-button>
      </router-link>
    </div>
    <el-row type="flex" justify="center">
      <el-col :xs="22" :sm="18" :md="14" :lg="10">
        <p class="complete-text">{{text}}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'SubmitJumpPage',
  props: {
    text: {
      type: String,
      default: '提交完成, 感谢您的参与'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "~common/styles/variable";
  .view-header {
    // width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $blue;
    color: $white;
    margin-bottom: 20px;
    h3 {
      font-size: 18px;
      font-weight: 700;
      line-height: 1.4;
      color: $white;
    }
  }
  .complete-text {
    color: $blue;
  }
</style>



