<template>
  <ve-bar :data="chartData" :settings="chartSettings" :legend-visible="false" :title="chartTitle" height="100%" :grid="grid" :colors="colors" :after-config="afterConfig" 
  :tooltip="tooltip" ref="chart" :mark-line="markLine" :data-empty="dataEmpty">
  </ve-bar>
</template>

<script>
import 'echarts/lib/component/title'
import 'v-charts/lib/style.css'
  export default {
    props: {
      titleText: {
        type: String,
        default: '数据占比'
      },
      rowData: {
        type: Array,
        default () {
          return []
        }
      }
    },
    data () {
      return {
        dataEmpty: false,
        chartTitle: {
          text: this.titleText,
          textStyle: {
            color: '#1d8ce0'
          }
        },
        grid: {
          top: '10%',
          bottom: '5%',
          right: '8%',
          left: '5%'
        },
        colors: ['#58b7ff'],
        tooltip: {
          // showContent: false
          formatter (params) {
            return params.seriesName + ': ' + params.data
          }
        },
        chartData: {
          columns: ['title', 'total'],
          rows: []
        },
        chartSettings: {
          label: {
            normal: {
              show: true,
              position: 'right',
              formatter: ({value}) => {
                return Math.round((value / this.totalNum).toFixed(2) * 100) + '%'
              }
            }
          }
        },
        totalNum: 0
      }
    },
    created: function () {
      // console.log(this.rowData)
      this.markLine = {
        lineStyle: {
          normal: {
            width: 8
          }
        }
      }
    },
    mounted () {
      // console.log(this.rowData)
      this.$nextTick(() => {
        this.$refs.chart.resize()
      })
    },
    methods: {
      afterConfig (options) { // 限制y轴文字长度
        options.xAxis.forEach((item) => {
          item.minInterval = 1
        })
        options.yAxis.forEach((item) => {
          item.axisLabel.formatter = (val) => {
            if (val.length > 30) {
              return val.slice(0, 30) + '...'
            }
            return val
          }
        })
        return options
      },
      calTotalNum () {
        this.totalNum = this.rowData.reduce((acc, cur) => acc.total + cur.total)
      }
    },
    watch: {
      rowData (val) {
        if (val.length) {
          this.$set(this.chartData, 'rows', val)
          this.totalNum = val.reduce((acc, cur) => {
            return acc + cur.total
          }, 0)
          if (val.length === 1 && val[0].total === 0) {
            this.dataEmpty = true
          }
        }
      }
    }
  }
</script>


