import Vue from 'vue'
import Router from 'vue-router'
import DefaultPage from 'components/DefaultPage/DefaultPage'
import NewListPage from 'components/NewListPage/NewListPage'
import WelcomePage from 'components/WelcomePage/WelcomePage'
import ViewPage from 'components/ViewPage/ViewPage'
import SubmitJumpPage from 'components/SubmitJumpPage/SubmitJumpPage'
import CountPage from 'components/CountPage/CountPage'
import axios from 'axios'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: WelcomePage
    },
    {
      path: '/list',
      component: DefaultPage
    },
    {
      path: '/create',
      component: NewListPage
    },
    {
      path: '/edit/:id',
      props: true,
      component: NewListPage
    },
    {
      path: '/view/:itemId',
      name: 'researchPage',
      props: true,
      component: ViewPage
    },
    {
      path: '/complete',
      component: SubmitJumpPage
    },
    {
      path: '/count/:id',
      props: true,
      component: CountPage
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})

router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('__USERTOKEN__')
  if (to.path === '/') {
    if (token) { // 已经登陆则跳到list页
      next('/list')
    }
    next()
  } 
  else if (to.name === 'researchPage') {
    next()
  } 
  else if (to.path === '/complete') {
    if (from.path.indexOf('view') > -1) {
      next()
    } else {
      next('/')
    }
  } 
  else {
    if (token) {
      next()
    } else {
      next('/')
    }
  }
})

export {router as default}



