export const setUserInfo = function ({commit}, data) {
  commit(types.SET_USER_INFO, data)
  localStorage.setItem('__USERINFO__', JSON.stringify(data))
  // localStorage.setItem('__USERITEM__', JSON.stringify(data))
}
export const removeUserInfo = function ({commit}) {
  commit(types.SET_USER_INFO, null)
  localStorage.removeItem('__USERINFO__')
  // localStorage.removeItem('__USERTOKEN__')
}


