<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    size="tiny"
    :custom-class="customClass"
    :before-close="handleCancleClick"
    >
    <div v-if="text">{{this.text}}</div>
    <p v-if="this.extraContent">{{this.extraContent}}</p>
    <slot></slot>
    <span slot="footer" class="dialog-footer" v-if="footer">
      <el-button type="primary" @click="handleClick">确 定</el-button>
      <el-button @click="handleCancleClick">取 消</el-button>
    </span>
  </el-dialog>
</template>
<script>
  export default {
    name: 'CustomDialog',
    // data() {
    //   return {
    //     title: this.props.title || '提示'
    //   }
    // },
    props: {
      text: {
        type: String,
        default: ''
      },
      dialogVisible: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: '提示'
      },
      extraContent: {
        type: String,
        default: ''
      },
      footer: {
        type: Boolean,
        default: true
      },
      customClass: {
        type: String,
        default: ''
      }
    },
    methods: {
      handleClick() {
        this.$emit('dialogConfirm')
      },
      handleCancleClick() {
        this.$emit('dialogCancel')
      }
    }
  }
</script>
<style scoped lang="scss">
</style>




